import React from 'react'
import Announcements from './Announcements/Announcements'
import {Pagination} from '@mui/material'
import './Announcement.css'
import axios from 'axios'
import { useEffect } from 'react'
import { useState } from 'react'

export default function Announcement({announceCategory}) {
    const SV = process.env.REACT_APP_SV_HOST;

    const [announcements, setAnnouncements] = useState([])
    const [display, setDisplay] = useState([])
    const [page, setPage] = useState(1)
    const [totalPage, setTotalPage] = useState(0)
    const [categoryName, setCategoryName] = useState('')
    const limit = 5
    const category = [
        {
            id: "cthhsv",
            name: "Phng Cng tc hc sinh sinh vin",
        },
        {
            id: "ptc",
            name: "Phng ti chnh",
        },
        {
            id: "tdtclc",
            name: "TDT Creative Language Center",
        },
        {
            id: "ttth",
            name: "Trung tm tin hc",
        },
        {
            id: "ttdtptxh",
            name: "Trung tm o to pht trin x hi",
        },
        {
            id: "ttptkh",
            name: "Trung tm pht trin Khoa hc qun l v ng dng cng ngh",
        },
        {
            id: "tthtdn",
            name: "Trung tm hp tc doanh nghip v cu sinh vin",
        },
        {
            id: "kl",
            name: "Khoa Lut",
        },
        {
            id: "ttnn",
            name: "Trung tm ngoi ng - tin hc - bi dng vn ha",
        },
        {
            id: "vcskt",
            name: "Vin chnh sch kinh t v kinh doanh",
        },
        {
            id: "kmtcn",
            name: "Khoa M thut cng nghip",
        },
        {
            id: "kddt",
            name: "Khoa in - in t",
        },
        {
            id: "kcntt",
            name: "Khoa Cng ngh thng tin",
        },
        {
            id: "kqtkd",
            name: "Khoa Qun tr kinh doanh",
        },
        {
            id: "kmt",
            name: "Khoa Mi trng v bo h lao ng",
        },
        {
            id: "kldcd",
            name: "Khoa Lao ng cng on",
        },
        {
            id: "ktcnn",
            name: "Khoa Ti chnh ngn hng",
        },
        {
            id: "kgdqt",
            name: "Khoa gio dc quc t",
        }
    ]

    const fetchAnnouncement = async () => {
        try{
            const res = announceCategory ? await axios.get(SV + '/announcements/' + announceCategory) : await axios.get(SV + '/announcements/')
            announceCategory ? setCategoryName(category.filter(c => c.id == announceCategory)[0].name) : setCategoryName("Tt c thng bo")
            setAnnouncements(res.data)
        }
        catch(err){

        }
    }

    useEffect(() => {
        fetchAnnouncement()
        
        
    }, [announceCategory])

    useEffect(() => {
        setTotalPage(Math.ceil(announcements.length/limit))
        const dp = announcements.slice((page - 1) * limit, page * limit)
        setDisplay([...dp])

    }, [page, announcements])


    const pageHandle = (event, value) => {
        setPage(value)
    }

    return (
        <div className="announcement">
            
            <div className="announcement-top">
                <div className="announcement-category">
                    {categoryName}
                </div>
            
            </div>
            <div className="announcement-bottom">
                {display.map(a => <Announcements key={a._id} announcement={a} />)}
                
                <Pagination count={totalPage} onChange={pageHandle} />
            </div>
        </div>
    )
}
