import React from 'react'
import './Register.css'
import { useRef, useState, useEffect } from 'react'
import { ArrowBack } from '@mui/icons-material'
import { Link } from 'react-router-dom'
import axios from 'axios'

export default function Register() {

    const SV = process.env.REACT_APP_SV_HOST

    const emailnameRef = useRef()
    const nameRef = useRef()
    const passwordRef = useRef()
    const confirmPasswordRef = useRef()

    const [permissions, setPermissions] = useState([])
    const [errorMessage, setErrorMessage] = useState("")

    const handleCheckbox = (e) => {
        if(e.target.checked){
            setPermissions([...permissions, e.target.value])
        }
        else{
            setPermissions(permissions.filter(p => p !== e.target.value))
        }
    }

    useEffect(() => {
        console.log(errorMessage)
    }, [errorMessage])

    const handleRegister = async (e) => {
        e.preventDefault()

        const emailname = emailnameRef.current.value
        const name = nameRef.current.value
        const password = passwordRef.current.value
        const confirmPassword = confirmPasswordRef.current.value
        if(emailname === "" || emailname === null){
            setErrorMessage("Please enter Username")
            return false
        }
        if(name === "" || name === null){
            setErrorMessage("Please enter Name")
            return false
        }
        if(password === "" || password === null){
            setErrorMessage("Please enter Password")
            return false
        }
        if(password !== confirmPassword){
            setErrorMessage("Password does not match")
            return false
        }
        if(permissions.length === 0){
            setErrorMessage("You must grant at least one permission")
            return false
        }

        try {
            const res = await axios.post(SV+"/auth/register", {emailname, name, password, permissions})
        }
        catch(err){

        }
    }

    return (
        <>
        <div className="register-form">
            <h1 className="register-logo">TDTU</h1>

            <form className="register" onSubmit={handleRegister}>
                <div className="register-input-group">
                    <div className="form-group">
                        <input type="text" placeholder="Username" ref={emailnameRef}/>
                    </div>
                    <br/>
                    <div className="form-group">
                        <input type="text" placeholder="Name" ref={nameRef}/>
                    </div>
                    <br/>
                    <div className="form-group">  
                        <input type="password" placeholder="Password" ref={passwordRef}/>
                    </div>
                    <br/>
                    <div className="form-group">  
                        <input type="password" placeholder="Confirm Password" ref={confirmPasswordRef}/>
                    </div>
                </div>

                <div className="register-checkbox-group1">
                    
                    <label><input type="checkbox" name="permission" value="Phng Cng tc hc sinh sinh vin" onChange={handleCheckbox} /> Phng Cng tc hc sinh sinh vin</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Phng i hc" onChange={handleCheckbox} /> Phng i hc</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Phng Sau i hc" onChange={handleCheckbox} /> Phng Sau i hc</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Phng in ton v my tnh" onChange={handleCheckbox} /> Phng in ton v my tnh</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Phng kho th v kim nh cht lng" onChange={handleCheckbox} /> Phng kho th v kim nh cht lng</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Phng ti chnh" onChange={handleCheckbox} /> Phng ti chnh</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="TDT Creative Language Center" onChange={handleCheckbox} /> TDT Creative Language Center</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Trung tm tin hc" onChange={handleCheckbox} /> Trung tm tin hc</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Trung tm o to pht trin x hi" onChange={handleCheckbox} /> Trung tm o to pht trin x hi</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Trung tm pht trin Khoa hc qun l v ng dng cng ngh" onChange={handleCheckbox} /> Trung tm pht trin Khoa hc qun l v ng dng cng ngh</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Trung tm hp tc doanh nghip v cu sinh vin" onChange={handleCheckbox} /> Trung tm hp tc doanh nghip v cu sinh vin</label>
                    
                </div>

                <div className="register-checkbox-group2">
                    
                    <label><input type="checkbox" name="permission" value="Khoa Lut" onChange={handleCheckbox} /> Khoa Lut</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Trung tm ngoi ng - tin hc - bi dng vn ha" onChange={handleCheckbox} /> Trung tm ngoi ng - tin hc - bi dng vn ha</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Vin chnh sch kinh t v kinh doanh" onChange={handleCheckbox} /> Vin chnh sch kinh t v kinh doanh</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa M thut cng nghip" onChange={handleCheckbox} /> Khoa M thut cng nghip</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa in - in t" onChange={handleCheckbox} /> Khoa in - in t</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa Cng ngh thng tin" onChange={handleCheckbox} /> Khoa Cng ngh thng tin</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa Qun tr kinh doanh" onChange={handleCheckbox} /> Khoa Qun tr kinh doanh</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa Mi trng v bo h lao ng" onChange={handleCheckbox} /> Khoa Mi trng v bo h lao ng</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa Lao ng cng on" onChange={handleCheckbox} /> Khoa Lao ng cng on</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa Ti chnh ngn hng" onChange={handleCheckbox} />  Khoa Ti chnh ngn hng</label>
                    <br />
                    <label><input type="checkbox" name="permission" value="Khoa gio dc quc t" onChange={handleCheckbox} />  Khoa gio dc quc t</label>
                </div>
                
                <button className="submit-btn" type="submit" >Register</button>
                <br />
            </form>
            <Link className="register-back" to="/"><ArrowBack /></Link>
        </div>
        </>
    )
}
